*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #cbbb9f;
}

:root {
  scrollbar-color: #cbbb9f transparent;
  scrollbar-width: thin;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

div {
  margin: 0;
}

p {
  padding: 0;
  margin: 0;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

button,
input {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
}

a {
  padding: 0;
  margin: 0;
  color: inherit;
  text-decoration: none;
}

img {
  display: flex;
  object-fit: cover;
}

a,
button,
input,
textarea {
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

body {
  font-size: 16px;
  line-height: 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  overflow: hidden;
}

.container {
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
}

.logo__img {
  width: 155px;
  height: 84px;
  transition: all 0.4s ease-in-out;
}
.logo__img:hover {
  opacity: 0.8;
}

.btn {
  position: relative;
  padding: 20px 25px;
  cursor: pointer;
}
.btn .btn__text {
  position: relative;
  z-index: 2;
  font-size: 18px;
  line-height: 22px;
  color: white;
  text-transform: uppercase;
  transition: all 0.4s ease-in-out;
}
.btn--fill::before, .btn--fill::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  border-radius: 8px;
  transition: all 0.4s ease-in-out;
}
.btn--fill::before {
  z-index: 1;
  background-color: #887569;
}
.btn--fill::after {
  border: 1px solid #887569;
  opacity: 0;
  transform: scale(1.2, 1.2);
}
.btn--fill:hover::before {
  opacity: 0;
  transform: scale(0.5, 0.5);
}
.btn--fill:hover::after {
  opacity: 1;
  transform: scale(1, 1);
}
.btn--fill:hover .btn__text {
  color: #887569;
}
.btn--fill:active::after {
  border-color: #958276;
}
.btn--transparent .btn__text {
  color: #887569;
}
.btn--transparent::before, .btn--transparent::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  border-radius: 8px;
  transition: all 0.4s ease-in-out;
}
.btn--transparent::after {
  z-index: 1;
  background-color: #887569;
  opacity: 0;
  transform: scale(1.2, 1.2);
}
.btn--transparent::before {
  border: 1px solid #887569;
  opacity: 1;
}
.btn--transparent:hover .btn__text {
  color: white;
}
.btn--transparent:hover::before {
  opacity: 0;
  transform: scale(0.5, 0.5);
}
.btn--transparent:hover::after {
  opacity: 1;
  transform: scale(1, 1);
}
.btn--transparent:active::after {
  background-color: #958276;
}

@media (max-width: 500px) {
  .btn {
    padding: 16px 20px;
  }
  .btn__text {
    font-size: 12px;
    line-height: 15px;
  }
}
.input {
  width: 100%;
  padding: 21px 29px;
  font-size: 16px;
  line-height: 20px;
